<template>
    <section class="basic-metrics">
      <div class="basic-metric">
        <div class="basic-metrics__label">{{ $t('metrics.total') }}</div>
        <div class="basic-metrics__value">
          <animated-number :number="metrics.revenueTotal || 0" :formatted="true" />
          {{ getSymbol(metrics.currencyISOCode) }}</div>
      </div>
      <div class="basic-metric">
        <div class="basic-metrics__label">{{ $t('metrics.conversion') }}</div>
        <div class="basic-metrics__value">
          <animated-number :number="metrics.basketConversionRate || 0" :formatted="true" />%
        </div>
      </div>
      <div class="basic-metric">
        <div class="basic-metrics__label">{{ $tc('metrics.order', metrics.ordersCountTotal) }}</div>
        <div class="basic-metrics__value">
          <animated-number :number="metrics.ordersCountTotal || 0" :formatted="true" />
        </div>
      </div>
    </section>
</template>

<script>
import symbolMixin from '../js/symbol-mixin'
import AnimatedNumber from './animated-number.vue'
export default {
  name: 'MetricsBasic',
  mixins: [symbolMixin],
  computed: {
    metrics () {
      return this.$store.getters.metrics
    }
  },
  components: {
    AnimatedNumber
  }
}
</script>

