<template>
  <div class="dashboard-container">
    <section class="dashboard">
      <settings-menu @currencyChanged="reloadMetrics" />
      <welcome />
      <metrics-basic />
      <button @click="toggleAdvanced" class="expand-trigger" :class="{'expanded': expanded}">{{ triggerText }}</button>
      <transition name="fade">
        <metrics-advanced v-show="expanded" @daysChanged="reloadMetrics" />
      </transition>
      <feed />
    </section>
  </div>
</template>

<script>
import Welcome from '../components/welcome-greeting.vue'
import MetricsBasic from '../components/metrics-basic.vue'
import MetricsAdvanced from '../components/metrics-advanced.vue'
import Feed from '../components/feed.vue'
import SettingsMenu from '../components/menu.vue'
export default {
  name: 'Dashboard',
  data () {
    return {
      metricTimer: null
    }
  },
  computed: {
    expanded () {
      return this.$store.getters.showAdvancedMetrics
    },
    triggerText () {
      return this.expanded ? this.$t('global.collapse') : this.$t('global.expand')
    },
    pollInterval () {
      return this.$store.getters.pollInterval
    },
    daysDisplayed () {
      return this.$store.getters.daysDisplayed
    },
    defaultCurrency () {
      return this.$store.getters.defaultCurrency
    }
  },
  components: {
    Welcome,
    MetricsBasic,
    MetricsAdvanced,
    Feed,
    SettingsMenu
  },
  methods: {
    toggleAdvanced () {
      this.$store.dispatch('showAdvancedMetrics', !this.expanded)
    },
    reloadMetrics () {
      window.clearTimeout(this.metricTimer)
      this.getMetrics()
    },
    getMetrics () {
      this.axios.get(`metrics/${this.daysDisplayed}/${this.defaultCurrency.value}`)
        .then(({ data }) => {
          this.$store.dispatch('setMetrics', data)
          this.metricTimer = window.setTimeout(() => {
            this.getMetrics()
          }, this.pollInterval)
        })
    },
    getCurrencies () {
      return this.axios.get('currencies')
    }
  },
  created () {
    this.getCurrencies()
      .then(({ data }) => {
        this.$store.dispatch('setCurrencies', data)
        this.getMetrics()
      })
  }
}
</script>

<style lang="stylus">
.dashboard-container
  page-container()

.dashboard
  width 600px
  position relative
  margin-top 65px

.expand-trigger
  border none
  background-color transparent
  color $col_primary
  padding 0
  margin-top 10px
  outline none
  font-size 0.725rem
  cursor pointer
  &:after
    font-size 0.5rem
    margin-left 3px
    content ''
  &.expanded:after
    content ''

</style>
