import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'
import VueI18n from 'vue-i18n'
import Axios from 'axios'
import messages from './translations/_main'
import { Format, locales } from '../shared/js/date-formatter'
import Marked from 'marked'

Vue.config.productionTip = false

// Setup Axios as http handler for Vue (including license interceptor)

const axiosInstance = Axios.create({
  baseURL: '/ucommerceapi/v2/'
})

axiosInstance.interceptors.response.use(response => {
  return response
}, error => {
  if (error.status === 610 || error.status === 620) {
    const routeName = error.status === 610 ? 'invalid' : 'not-conected'
    router.replace({ name: routeName })
    store.dispatch('setLicenseError', error.data)
  }
  return Promise.reject(error)
})
Vue.prototype.axios = axiosInstance

// Language versioning lib setup

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages
})

// Global filters

Vue.filter('format', function (value, format, locale) {
  if (!value) return ''
  value = value.toString()
  return Format(value, format, locales[locale])
})

Vue.filter('markdown', function (value) {
  if (!value) return ''
  value = value.toString()
  return Marked(value, {
    gfm: true,
    breaks: true,
    sanitize: true
  })
})

new Vue({
  store,
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
