<template>
  <section class="advanced-metrics">
    <div class="advanced-metrics__intervals">
      <button ref="dayBtn" @click="changeInterval($event, 1)" class="interval-toggle active">{{ $t('metrics.day') }}</button>
      <button ref="monthBtn" @click="changeInterval($event, 30)" class="interval-toggle">{{ $t('metrics.month') }}</button>
      <button ref="yearBtn" @click="changeInterval($event, 365)" class="interval-toggle">{{ $t('metrics.year') }}</button>
    </div>
    <div v-for="storeStatistics in metrics" class="advanced-metrics__section" :key="storeStatistics.name">
      <div class="advanced-metrics__store-header">
        <div class="store__name">{{storeStatistics.name}}</div>
        <span class="advanced-metrics__interval">{{ timeInterval }}</span>
      </div>

      <div v-if="!storeStatistics.revenues.length" class="revenue-row">
          <span class="revenue-value">-</span>
          <span class="revenue__order">0 {{$tc('metrics.order', 0)}}</span>
      </div>
      <div class="revenue-row" :key="revenue.ISOCode" v-for="revenue in storeStatistics.revenues">
        <div>
            <span class="revenue-value">{{ `${formatRevenue(revenue.revenueLastPeriod, revenue.amount)} ${getSymbol(revenue.ISOCode)}` }} </span>
          <span class="revenue__difference" :class="{'positive': revenue.differenceInPercentageLastPeriod >= 0}">{{ revenue.differenceInPercentageLastPeriod }}% </span>
        </div>
        <div>
          <span class="revenue__order">{{ revenue.ordersCount}} {{$tc('metrics.order', revenue.ordersCount)}}</span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import symbolMixin from '../js/symbol-mixin'
import { Format, locales } from '../../shared/js/date-formatter'
const ACTIVE = 'active'

export default {
  name: 'MetricsAdvanced',
  mixins: [symbolMixin],
  computed: {
    metrics () {
      return this.$store.getters.metrics.storeRevenueStatistics
    },
    timeInterval () {
      const days = this.$store.getters.daysDisplayed
      const locale = locales[this.$store.getters.locale]
      const format = days > 300 ? 'MMM DD, YYYY' : 'MMM DD'
      const date = new Date()
      const rightDate = Format(date, format, locale)
      const leftDate = days > 1 ? `${Format(date.setDate(date.getDate() - days), format, locale)} -` : ''
      return `${leftDate} ${rightDate}`
    }
  },
  methods: {
    changeInterval (event, days) {
      this.$refs.dayBtn.classList.remove(ACTIVE)
      this.$refs.monthBtn.classList.remove(ACTIVE)
      this.$refs.yearBtn.classList.remove(ACTIVE)
      event.target.classList.add(ACTIVE)
      this.$store.dispatch('setDaysShown', days)
      this.$emit('daysChanged')
    },
    formatRevenue (lastPeriod, amount) {
      const fixedSum = +(lastPeriod + amount).toFixed(0)
      return fixedSum.toLocaleString()
    }
  }
}
</script>

<style lang="stylus">
.advanced-metrics
  background-color #fbfbfb
  box-shadow 1px 1px 5px rgba(0,0,0,0.3)
  border-radius 2px
  margin-top 15px
  min-height 150px

.advanced-metrics__intervals
  padding 10px 0 0 20px

.interval-toggle
  border none
  margin-right 8px
  font-size 0.875rem
  color $col_dark
  background-color #F1EFEF
  font-weight 600
  padding 6px 15px 8px
  border-radius 3px
  outline none
  cursor pointer
  &.active
    background-color #DCDCDC
    pointer-events none
    cursor default

.advanced-metrics__section
    border-bottom: 1px solid rgba(0,0,0,0.1)
    padding 10px 20px 20px
    &:last-child
      border-bottom none

.advanced-metrics__store-header
  display flex
  justify-content space-between
  color: $col_dark_gray
  margin-top: 12px
  margin-bottom: 12px
  font-size 0.8125rem

.revenue-row
  display: flex
  justify-content: space-between
  align-items baseline
  margin-bottom: 6px

.revenue-value
  font-weight: 600
  font-size 1.5rem

.revenue__difference
  color $col_red
  font-size 0.625rem
  font-weight 600
  display inline-block
  &:before
    content ''
    font-size 0.5rem
    margin-right 3px
    margin-left 8px
  &.positive
    color $col_green
    &:before
      content ''

.revenue__order
  font-size 0.875rem

</style>
