<template>
  <button class="ucom-button" @click="clickHandler">{{btnText}}<slot></slot></button>
</template>

<script>
export default {
  name: 'custom-button',
  props: {
    url: String,
    btnText: String
  },
  methods: {
    clickHandler (event) {
      if (this.url) {
        window.location.href = this.url
        return
      }
      this.$emit('click', event)
    }
  }
}
</script>

<style lang="stylus">
.ucom-button
  background-color $col_primary
  color #fff
  display block
  line-height 55px
  border none
  border-radius 10px
  font-size 1.125rem
  font-weight 600
  padding 0 60px
  box-shadow 2px 3px 10px rgba(0,0,0,0.3)
  transition all 150ms
  outline none
  cursor pointer
  &:hover
    background-color lighten($col_primary, 5)
  &:active
    box-shadow 0 0 0 rgba(0,0,0,0.3)
</style>
