<template>
  <div class="license-container">
    <div class="license-page">
      <div class="license__icon">
        <component :is="iconType"></component>
      </div>
      <h1 class="license__heading">{{heading}}</h1>
      <section class="license__text" v-html="$options.filters.markdown(bodyText)">
      </section>
      <div class="license__cta">
        <btn @click="buttonClick">{{buttonText}}</btn>
      </div>
      <h3 class="license__error-details-header">{{ $t('license.errorDetails') }}</h3>
      <section class="license__error-details">{{errorText}}</section>
    </div>
  </div>
</template>

<script>
import ExclamationIcon from '../../shared/icons/exclamation.vue'
import NoConnectIcon from '../../shared/icons/route-close.vue'
import Btn from '../components/btn.vue'
export default {
  name: 'license-error',
  props: {
    errorType: String
  },
  computed: {
    invalid () {
      return this.errorType === 'invalid'
    },
    iconType () {
      return this.invalid ? 'ExclamationIcon' : 'NoConnectIcon'
    },
    heading () {
      const translateKey = this.invalid ? 'license.invalidHeading' : 'license.noConnectionHeading'
      return this.$t(translateKey)
    },
    bodyText () {
      const translateKey = this.invalid ? 'license.invalidBody' : 'license.noConnectionBody'
      return this.$t(translateKey)
    },
    buttonText () {
      const translateKey = this.invalid ? 'license.checkLicense' : 'license.checkConnection'
      return this.$t(translateKey)
    },
    errorText () {
      return this.$store.getters.licenseError
    }
  },
  methods: {
    buttonClick () {
      this.axios.get('licensing/licensevalidity')
        .then(({ data }) => {
          // Do something meaningful here - reload if succesful..
        })
    }
  },
  components: {
    ExclamationIcon,
    NoConnectIcon,
    Btn
  }
}
</script>

<style lang="stylus">
.license-container
  page-container()
.license-page
  width 600px
  position relative
  margin-top 65px
.license__icon
  box(45px)
.license__heading
  margin-top 70px
.license__text
  font-size 1.2rem
  color $col_dark_gray
  width 500px
  margin-top 60p
  a
    color inherit
.license__cta
  margin-top 70px
.license__error-details-header
  font-size 0.875rem
  margin-top 100px
.license__error-details
  background-color #FFF9E3
  padding 20px 15px
  font-family: Courier New, Courier, Lucida Sans Typewriter, Lucida Typewriter, monospace

</style>
