<template>
  <div class="feed__item" :class="classes">
      <div class="feed__item-icon-wrap">
        <component :is="notification.type" />
      </div>
      <div class="feed__item-body-wrap">
        <span class="feed__item-date">{{notification.created | format('ddd D, hh:mm', locale)}}</span>
        <h3 class="feed__item-heading" v-if="showHeading">{{ heading }}</h3>
        <p class="feed__item-text">
          {{bodyText}}
        </p>
        <a class="feed__item-link" target="_self" v-if="notification.url" :href="notification.url"><open-new-icon />{{linkText}}</a>
      </div>
      <div class="feed__item-menu-wrap">
        <!-- <menu-icon /> -->
      </div>
      <div v-if="hasBundle" class="feed__item-bulk-notification" :class="classes">
        <div></div>
        <div>
          <span>{{`+${notification.bundle.length} ${$tc('feed.others', notification.bundle.length)}`}} </span>
          <a href="#" @click.prevent="toggleBundle">{{toggleLinkText}}</a>
        </div>
      </div>
    </div>
</template>

<script>
import CustomerIcon from '../../shared/icons/customer.vue'
import CustomerNewIcon from '../../shared/icons/customer_new.vue'
import HandCardIcon from '../../shared/icons/hand-card.vue'
import OrderIcon from '../../shared/icons/order.vue'
import CampaignIcon from '../../shared/icons/tag.vue'
import MenuIcon from '../../shared/icons/menu.vue'
import OpenNewIcon from '../../shared/icons/open-in-new.vue'
import NoConnectIcon from '../../shared/icons/route-close.vue'
import ConnectedIcon from '../../shared/icons/route-open.vue'
import { mapGetters } from 'vuex'
import symbolMixin from '../js/symbol-mixin'

export default {
  name: 'feed-card',
  mixins: [symbolMixin],
  props: {
    notification: Object,
    isChild: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classes () {
      const type = this.notification.type
      return {
        'has-bundle': this.hasBundle,
        'bundle-shown': this.notification.showBundle,
        'user': type === this.feedTypes.newCustomer.type || type === this.feedTypes.returningCustomer.type,
        'order': type === this.feedTypes.orderPlaced.type,
        'campaign': type === this.feedTypes.campaignTriggered.type,
        'checkout': type === this.feedTypes.customerIsCheckingOut.type,
        'no-connection': type === this.feedTypes.licenseNetworkUnreachable.type,
        'connection-established': type === this.feedTypes.licenseNetworkReachable.type,
        'is-child': this.isChild
      }
    },
    linkText () {
      switch (this.notification.type) {
        case this.feedTypes.orderPlaced.type:
          return this.$t('feed.openOrder')
        case this.feedTypes.campaignTriggered.type:
          return this.$t('feed.openCampaign')
        case this.feedTypes.customerIsCheckingOut.type:
          return this.$t('feed.openBasket')
        default:
          return ''
      }
    },
    bodyText () {
      switch (this.notification.type) {
        case this.feedTypes.newCustomer.type:
          return this.$t('feed.body.newCustomer', { customer: this.notification.customerName })
        case this.feedTypes.returningCustomer.type:
          return this.$t('feed.body.returningCustomer', { customer: this.notification.customerName, orderAmount: this.notification.orderAmount })
        case this.feedTypes.orderPlaced.type:
          return this.$t('feed.body.orderPlaced', { customer: this.notification.customerName, order: this.notification.orderNumber, orderTotal: `${this.notification.amount.toLocaleString()} ${this.getSymbol(this.notification.isoCode)}` })
        case this.feedTypes.campaignTriggered.type:
          return this.$t('feed.body.campaignTriggered', { campaignName: this.notification.campaignName, campaignItemName: this.notification.campaignItemName })
        case this.feedTypes.customerIsCheckingOut.type:
          return this.$t('feed.body.customerIsCheckingOut', { customer: this.notification.customerName, customerContact: this.notification.customerPhone || this.notification.email })
        case this.feedTypes.licenseNetworkUnreachable.type:
          return this.$t('feed.body.licenseNetworkUnreachable', { stopDate: this.notification.stopDate })
        case this.feedTypes.licenseNetworkReachable.type:
          return this.$t('feed.body.licenseNetworkReachable')
        default:
          return ''
      }
    },
    hasBundle () {
      return this.notification.bundle.length > 0
    },
    bundleShown () {
      return this.notification.showBundle
    },
    toggleLinkText () {
      return this.notification.showBundle ? this.$t('feed.hideOthers') : this.$t('feed.seeAll')
    },
    showHeading () {
      return (this.notification.type === this.feedTypes.licenseNetworkUnreachable.type) || (this.notification.type === this.feedTypes.licenseNetworkReachable.type)
    },
    heading () {
      const key = this.notification.type === this.feedTypes.licenseNetworkUnreachable.type ? 'feed.noConnectionHeading' : 'feed.connectedHeading'
      return this.$t(key)
    },
    ...mapGetters(['locale', 'feedTypes'])
  },
  methods: {
    toggleBundle () {
      this.$store.dispatch('showBundle', { itemId: this.notification.id, show: !this.notification.showBundle })
    }
  },
  components: {
    'returningCustomer': CustomerIcon,
    'newCustomer': CustomerNewIcon,
    'customerIsCheckingOut': HandCardIcon,
    'orderPlaced': OrderIcon,
    'campaignTriggered': CampaignIcon,
    'licenseNetworkUnreachable': NoConnectIcon,
    'licenseNetworkReachable': ConnectedIcon,
    MenuIcon,
    OpenNewIcon
  }
}
</script>

<style lang="stylus">
.feed__item
  border-radius 3px
  background-color #fff
  margin-bottom 2px
  border solid 1px $col_light_border
  border-left-width 2px
  display grid
  grid-template-columns 0.8fr 6fr 1fr
  grid-template-rows: auto
  position relative
  z-index 5
  transition all 300ms ease
  &.user
    border-left-color $col_dark_gray
  &.campaign
    border-left-color $col_yellow
  &.order
    border-left-color $col_green
  &.checkout
    border-left-color $col_blue
  &.no-connection
    border-left-color $col_red
    background-color $col_yellow_bg
  &.connection-establisehd
    border-left-color $col_green
    background-color $col_yellow_bg
  p
    margin 0.6em 0
  &.has-bundle
    box-shadow: 0px 5px 13px -5px rgba(0,0,0,0.3)
  &.bundle-shown
    box-shadow: 0 0 0 0 rgba(0,0,0,0.0)
  &.is-child
    z-index 3

.feed__item-bulk-notification
  grid-column: 1 / 4
  display grid
  grid-template-columns 0.8fr 6fr 1fr
  font-size 0.8125rem
  border-top dashed 1px $col_light_border
  padding 5px 0
  a
    color $col_primary

.feed__item-icon-wrap
  padding-top 10px
  padding-left 15px
  svg
    box(24px)
    margin-top 6px

.feed__item-body-wrap
  padding-top 10px
  padding-bottom 15px

.feed__item-menu-wrap
  padding-top 10px
  padding-right 15px
  display flex
  justify-content flex-end
  svg
    box(16px)

.feed__item-date
  font-weight 600
  font-size 0.6875rem
  color $col_medium_gray

.feed__item-text
  .is-priority &
    font-size 0.875rem

.feed__item-link
  color $col_primary
  font-size 0.875rem
  svg
    box(14px)
    fill $col_primary
    vertical-align middle
    margin-right 5px

</style>
