<template>
  <section class="feed">
    <h2 class="feed__heading">{{$t('feed.yourFeed')}}</h2>
    <div class="feed__no-content" v-if="emptyFeed">
      <p>
        {{$t('feed.noItems')}}
      </p>
      <div class="feed__no-content-icon">
        <spider-icon />
      </div>
    </div>
    <div class="feed__priority-list" v-if="priorityFeed.length > 0">
      <transition-group name="slide-list">
        <notification v-for="item in priorityFeed" :notification="item" :key="item.id" />
      </transition-group>
    </div>
    <transition-group name="slide-list">
      <notification v-for="item in feed" :notification="item" :key="item.id" />
    </transition-group>
  </section>
</template>

<script>
import Notification from './feed-item'
import SpiderIcon from '../../shared/icons/spider.vue'
import { mapGetters } from 'vuex'
export default {
  name: 'feed',
  computed: {
    emptyFeed () {
      return (this.feed.length + this.priorityFeed.length) === 0
    },
    ...mapGetters(['feed', 'priorityFeed', 'pollInterval'])
  },
  methods: {
    getFeed () {
      this.axios.get('feed')
        .then(({ data }) => {
          this.$store.dispatch('setFeed', data)
          window.setTimeout(() => {
            this.getFeed()
          }, this.pollInterval)
        })
        .catch(() => {
          window.setTimeout(() => {
            this.getFeed()
          }, this.pollInterval)
        })
    }
  },
  components: {
    Notification,
    SpiderIcon
  },
  created () {
    this.getFeed()
  }
}
</script>

<style lang="stylus">
.feed
  margin-top 50px
  position relative

.feed__heading
  font-weight 600
  font-size 1.125rem
  color $col_dark

.feed__no-content
  margin-top 30px
  padding 15px 0 35px
  display flex
  flex-direction column
  justify-elements center
  align-items center
  color $col_medium_gray
  border solid 1px $col_light_border

.feed__no-content-icon
  box(45px)

.feed__priority-list
  margin-bottom 20px

</style>
