<template>
  <div class="feed__item-wrapper" v-show="!muted">
    <feed-card :notification="notification" />
    <transition name="fade">
      <div v-if="hasBundle && !bundleShown" class="feed__item-stack one"></div>
      <div v-if="hasBundle && notification.bundle.length > 1 && !bundleShown" class="feed__item-stack two"></div>
      <div v-if="hasBundle && notification.bundle.length > 2 && !bundleShown" class="feed__item-stack three"></div>
    </transition>
    <transition-group name="flip-list">
      <feed-card v-for="subItem in bundle" :notification="subItem" :is-child="true" :key="subItem.id" />
    </transition-group>
  </div>
</template>

<script>
import FeedCard from './feed-card'
export default {
  name: 'feed-item',
  props: {
    notification: Object
  },
  computed: {
    hasBundle () {
      return this.notification.bundle.length > 0
    },
    bundleShown () {
      return this.notification.showBundle
    },
    bundle () {
      return this.notification.showBundle ? this.notification.bundle : []
    },
    muted () {
      return this.feedSettings[this.notification.type].muted
    },
    feedSettings () {
      return this.$store.getters.feedTypes
    }
  },
  components: {
    FeedCard
  }
}
</script>

<style lang="stylus">
.feed__item-wrapper
  position relative

.feed__item-stack
  border-radius 3px
  background-color #fff
  margin-bottom 2px
  height 8px
  position relative
  box-shadow: 0px 2px 5px -2px rgba(0,0,0,0.3)
  &.one
    z-index 4
    margin -8px 1px 4px
  &.two
    z-index 3
    margin -10px 2px 5px
  &.three
    margin -15px 3px 6px
    z-index 2

</style>
