<template>
  <div class="menu-container">
    <div class="menu-trigger" @click="toggleMenu">
      <coq-icon></coq-icon>
    </div>
    <nav class="dropdown-menu" :class="{'show': showMenu}">
      <span class="dropdown__label">{{$t('global.currency')}}</span>
      <ul class="dropdown__currency-list">
        <li v-for="(menuItem, index) in currencyMenu" :key="index" @click="selectDefault($event, menuItem.value)" :class="{selected: menuItem.selected}">
          <check-icon v-if="menuItem.selected"></check-icon>
          <span>{{menuItem.displayText}}</span>
        </li>
      </ul>
      <span class="dropdown__label">{{$t('global.shownFeed')}}</span>
      <ul>
        <li v-for="(menuItem, key, index) in mutableMenuItems" :key="index" @click="toggleFeed($event, key)" :class="{muted: menuItem.muted}">
          <hide-icon v-if="menuItem.muted" />
          <span>{{$t(`feed.types.${menuItem.type}`)}}</span>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import HideIcon from '../../shared/icons/not-visible.vue'
import CheckIcon from '../../shared/icons/check.vue'
import CoqIcon from '../../shared/icons/coq.vue'
import { mapGetters } from 'vuex'
export default {
  name: 'settings-menu',
  computed: {
    ...mapGetters(['feedTypes', 'currencyMenu', 'showMenu']),
    mutableMenuItems () {
      const mutableFeedtypes = {}
      Object.keys(this.feedTypes).forEach(type => {
        if (this.feedTypes[type].mutable) mutableFeedtypes[type] = this.feedTypes[type]
      })
      return mutableFeedtypes
    }
  },
  methods: {
    selectDefault (event, currencyId) {
      event.stopPropagation()
      this.$store.dispatch('selectDefaultCurrency', currencyId)
      this.$emit('currencyChanged')
    },
    toggleFeed (event, type) {
      event.stopPropagation()
      this.$store.dispatch('toggleFeedType', type)
    },
    toggleMenu () {
      this.$store.dispatch('showMenu', !this.showMenu)
      if (this.showMenu) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeMenuHandler)
        }, 10)
      }
    },
    closeMenuHandler (event) {
      event.stopPropagation()
      this.$store.dispatch('showMenu', false)
      window.removeEventListener('click', this.closeMenuHandler)
    }
  },
  components: {
    HideIcon,
    CheckIcon,
    CoqIcon
  }
}
</script>

<style lang="stylus">
.menu-container
  margin-left auto
  position absolute
  right 0
  z-index 100

.menu-trigger
  circle(26px)
  display flex
  cursor pointer
  transition background-color 200ms
  svg
    box(80%)
    fill rgba(0,0,0,0.2)
    margin auto
  &:hover
    background-color #e2e2e2

.dropdown-menu
  background-color #fff
  display inline-block
  min-width 130px
  position absolute
  right 0
  top 100%
  pointer-events none
  padding 10px 20px 8px 40px
  box-shadow 2px 2px 6px rgba(0,0,0,0.3)
  z-index 20
  transform-origin right top
  transform translate(-11px, -11px) scale(0)
  transition transform 200ms $easeInOutCirc
  ul
    zero-list()
    margin 0 -20px 10px -40px
  li
    padding 4px 20px 4px 40px
    cursor pointer
    display flex
    white-space nowrap
    position relative
    &:hover
      background-color rgba(0,0,0,0.03)
    &.selected
      pointer-events none
      cursor default
    &.muted
      color $col_medium_gray
      svg
        fill $col_medium_gray
    svg
      box(16px)
      fill rgba(0,0,0,0.3)
      position absolute
      left 10px
      top 7px
  &.show
    transform translate(0, 0) scale(1)
    pointer-events all

ul.dropdown__currency-list
   padding-bottom 10px
   border-bottom solid 1px rgba(0, 0, 0, 0.1)

.dropdown__label
  font-size 0.8125rem
  color $col_medium_gray
  display inline-block
  margin-bottom 6px

</style>
